import { AutoTrack, Config, DefaultLogger, EventComponent, EVENT_SDK_VERSION_DEFAULT_VAL } from '@abetterchoice/abc_web_core';
import { WxEventStorage } from './wx_event_storage';
import { WxEventReporter } from './wx_event_reporter';

// @ts-ignore
// eslint-disable-next-line no-undef
const env = wx;
// @ts-ignore
// eslint-disable-next-line no-undef
const SDK_VERSION = TABSDK_VERSION || EVENT_SDK_VERSION_DEFAULT_VAL; // 
export class WxEventComponent extends EventComponent {
  private throttleInterval = 1000;
  private lastShowTime: number;
  private lastHideTime: number;

  constructor(config: Config) {
    super(config);
  }
  protected initAutoTrack(autoTrack: AutoTrack | undefined) {
    // 
    if (!autoTrack) return;
    if (autoTrack.mgShow) {
      this.initAutoTrackShow();
    }
    if (autoTrack.mgHide) {
      this.initAutoTrackHide();
    }
    if (autoTrack.mgShare) {
      this.initAutoTrackShare();
    }
  }

  protected initEventReport() {
    this.eventReporter = new WxEventReporter(this.config, SDK_VERSION);
  }

  protected initEventStorage() {
    this.eventStorage = new WxEventStorage(this.config, SDK_VERSION);
  }

  private initAutoTrackShow() {
    env.onShow((result) => {
      const nowShowTime = Date.now();
      if (nowShowTime - this.lastShowTime < this.throttleInterval) {
        DefaultLogger.debug('onShow Throttling in effect, skipping this mgShow');
        return;
      }
      this.lastShowTime = nowShowTime;

      this.track('$mgShow', {
        scene: String(result.scene),
      }).then((result) => {
        DefaultLogger.debug(`$mgShow track event : ${result}`);
      });
    });
  }

  private initAutoTrackHide() {
    env.onHide((result) => {
      const nowHideTime = Date.now();
      if (nowHideTime - this.lastHideTime < this.throttleInterval) {
        DefaultLogger.debug('onHide Throttling in effect, skipping this mgHide');
        return;
      }
      this.lastHideTime = nowHideTime;
      this.track('$mgHide', {
        mode: result.mode,
      }).then((result) => {
        DefaultLogger.debug(`$mgHide track event : ${result}`);
      });
    });
  }

  private initAutoTrackShare() {
    const that = this;
    const shareAppMessageListener = function (res) {
      that.track('$mgShare').then((result) => {
        DefaultLogger.debug(`$mgShare track event : ${result}`);
      });
    };
    const shareTimelineListener = function (res) {
      that.track('$mgShare').then((result) => {
        DefaultLogger.debug(`$mgShare track event : ${result}`);
      });
    };
    env.onShareAppMessage(shareAppMessageListener);
    env.onShareTimeline(shareTimelineListener);
  }
}
